import instance from "../../../api/index.js"
export default{
	data() {
	   return {
	      btnnum: 0,
		  token:"",
		  list:[],
		  img:''
	   }
	},
	methods:{
		clickLeft(){
			uni.navigateBack(1)
		},
	   change(e) {
	      this.btnnum = e
	      console.log(this.btnnum)
	  },
	  todetail(orderNo){
		  uni.navigateTo({
		  	url:"oderdetail/orderdetail?orderNo="+orderNo
		  })
	  }
	},
	computed:{
		shouhou(){
			return this.list.filter(item=>item.orderStatus=="refund")
		}
	}, 
	onLoad(options) {
		this.btnnum = options.index
		this.token=uni.getStorageSync("token");
		instance({
			url:"/api/order/queryOrderList",
			data:{
				token:this.token,
				orderStatus:"",
				limit:"10",
				start:'0'
			},
			method:'POST',
			header:{
			   'content-type':'application/json',
			}
		}).then(res=>{
			let img=[]
			console.log(res)
			res.data.data.rows.forEach(item=>{
				img.push(item.orderProductsList[0])
			})
			console.log(img)
			this.img=img
			
			this.list=res.data.data.rows
		})
	}
}
